* {
  box-sizing: border-box;
}

body {
  background-color: gray;
  background-size: cover;
}

#wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

#game {
  background: #000;
  display: inline-grid;
  position: relative;
}

.game-status {
  width: 100%;
  height: 50px;
  color: #fff;
  background: #000;
  border: 5px solid #00008b;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#score {
  width: 200px;
  height: 50px;
  color: #fff;
  background: #000;
  border: 5px solid #00008b;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2rem;
  display: flex;
}

#start-button {
  margin-top: 20px;
}

.hide {
  display: none;
}

.square {
  display: block;
}

.blank {
  background: #000;
}

.wall {
  border: 1px solid #00f;
}

.dot {
  background: #fff;
  border: 8px solid #000;
  border-radius: 50%;
}

.pill {
  background: #fff;
  border: 5px solid #000;
  border-radius: 50%;
}

.ghost {
  border: none;
  border-radius: 10px 10px 2px 2px;
  position: relative;
}

.ghost:before {
  content: "";
  width: 3px;
  height: 3px;
  background: #000;
  border: 2px solid #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 5px;
  left: 3px;
}

.ghost:after {
  content: "";
  width: 3px;
  height: 3px;
  background: #000;
  border: 2px solid #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 5px;
  right: 3px;
}

.scared {
  background: #00f !important;
}

.blinky {
  background: red;
}

.pinky {
  background: pink;
}

.inky {
  background: #0ff;
}

.clyde {
  background: orange;
}

.pacman {
  clip-path: polygon(0 0, 20px 0, 20px -5px, 8px 10px, 20px 25px, 20px 20px, 0 20px);
  background: #ff0;
  border: none;
  border-radius: 50%;
  animation: chew .2s linear infinite forwards;
}

@keyframes chew {
  50% {
    clip-path: polygon(0 0, 20px 0, 20px 10px, 8px 10px, 20px 10px, 20px 20px, 0 20px);
  }
}

